// --- lineo psa blue fix ---
.highlight table td {
  padding: 0;
}

.rouge-gutter .lineno, .rouge-code pre, pre.highlight .gl {
  background-color: unset;
}

.rouge-code pre {
  color: white;
}
.rouge-gutter .lineno {
  color: #959595;
}

.rouge-gutter .lineno {
  padding-right: 0;
  padding-left: 0;
}

.rouge-gutter .lineno, .rouge-code pre {
  padding-top: 0;
  padding-bottom: 0;
}

// --- end lineo fix---



